import React from "react";
import {graphql} from "gatsby"
import Layout from "../Components/Layout";
import SimpleReactLightbox, {SRLWrapper} from "simple-react-lightbox";
import {SEO} from "theme-seo";
import Img from "gatsby-image";

const options = {
    settings: {
        disablePanzoom: true
    },
    caption: {
        captionContainerPadding: '10px'
    },
    buttons: {
        showThumbnailsButton:false,
        showAutoplayButton: false,
        showDownloadButton: false
    },
}


const Features = ({data, pageContext}) => {
    const markdownFiles = data.allMarkdownRemark.nodes
        .sort((a, b) => new Date(a.parent.birthTime) - new Date(b.parent.birthTime))
        .sort((a, b) => b.frontmatter.order - a.frontmatter.order);

    const captions = markdownFiles.map((node, index) => ({id: index, caption: node.html}));

    return (
        <Layout>
            <SEO title={pageContext.title}/>
            {
                data.allBannerImages.nodes.length !== 0 && (data.allBannerImages.nodes[0].extension === "svg" ?
                    <img style={{width: "100%"}} alt={data.allBannerImages.nodes[0].name}
                         src={data.allBannerImages.nodes[0].publicURL}/> :
                    <Img alt={data.allBannerImages.nodes[0].name}
                         fluid={data.allBannerImages.nodes[0].childImageSharp.fluid}/>)

            }

            <SimpleReactLightbox>
                <SRLWrapper options={options} customCaptions={captions}>
                    <div
                        className="flex flex-wrap my-24 justify-center md:flex-col lg:flex-row lg:justify-start w-10/12 lg:w-4/5 mx-auto shadow-xl">
                        {markdownFiles ? (markdownFiles.map(node => (
                            <div key={node.frontmatter.title} className="md:w-11/12 lg:w-1/3 my-3"
                                 data-aos="fade-up" data-aos-duration="800">
                                <div className="p-2 m-3">

                                    {data.allFeatureImages.nodes.filter(image => node.fields.slug.includes(image.name)).map(n => (

                                        <div className="mx-2 p-2" key={node.fields.slug}>
                                            <a href={n.childImageSharp.fixed.src}>
                                                <img className="m-auto w-full h-full"
                                                     src={n.childImageSharp.fixed.src}
                                                     alt={node.frontmatter.title}
                                                     width="333" height="177"
                                                />
                                            </a>

                                        </div>
                                    ))}
                                    <div className="text-3xl m-4">{node.frontmatter.title}</div>
                                    <div className="text-gray-600 mx-4" dangerouslySetInnerHTML={{__html: node.html}}></div>
                                </div>

                        </div>
           	 ))) : (<div className="m-auto text-purple-600 my-10 font-bold text-4xl">There is no feature to show</div>)}
                </div>
            </SRLWrapper>

        </SimpleReactLightbox>
    </Layout>

);}

export default Features;

export const pageQuery = graphql`
  query allFeatures {
  allMarkdownRemark(filter: {fileAbsolutePath: {regex: "/feature/"  }})
   {
    nodes {
      html
      fields {
        slug
      }
      frontmatter {
        description
        title
        order
      }
       parent {
          ... on File {
            birthTime(formatString: "MM/DD/YYYY")
          }
        }
    }
  }
 allFeatureImages: allFile(filter: { sourceInstanceName: { eq: "feature" }, extension:{eq: "png"} }) {
    nodes {
      name
      childImageSharp {
        fixed(width:1200){
         src
         }
       }
    }
  }
  allBannerImages: allFile(filter: { sourceInstanceName: { eq: "feature" }, name:{eq:"feature"}}) {
    nodes {
      name 
      publicURL
      extension
      childImageSharp {
        fluid (maxWidth: 2500 ) {
          ...GatsbyImageSharpFluid
        }
       }
    }
  }
}`;